body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__hand--minutes {
  transform-origin: 50%;
  z-index: 1;
  background-color: #0700ff;
  width: 20vmin;
  height: 3vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)rotate(90deg);
}

.stopwatch__center {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__hand--seconds {
  z-index: 2;
  transform-origin: 50%;
  background-color: #2c8000;
  width: 38vmin;
  height: 1.5vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)rotate(90deg);
}

.stopwatch--speed-up .stopwatch__hand--minutes {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__hand--seconds {
  animation-duration: 10s;
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotate(90deg)translateX(-50%);
  }

  100% {
    transform: translate(-50%, -50%)rotate(450deg)translateX(-50%);
  }
}
